.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 128px;
}

.main h1 {
  margin: var(--margin) 0;
}

.title {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: flex-end;
}

.version {
  padding-left: 8px;
  padding-bottom: calc(0.45 * var(--size-lg));
  color: var(--gray);
  font-size: var(--size-tn);
}

.dropdown {
}

.dropdowns select {
  padding: 2px 4px;
  border: var(--border);
  border-radius: 4px;
  background-color: var(--white);
  font-size: var(--size-md);
  margin-top: 8px;
}

.separator {
  margin: 0 8px;
  font-size: var(--size-md);
}

.description {
  max-width: 450px;
  margin-top: var(--margin);
  font-size: var(--size-sm);
  color: var(--gray);
  text-align: center;
}

.display {
  margin-top: var(--margin);
}
